          <b-card no-body class="invoice-preview-card">
            <!-- Header -->
            <b-card-body class="invoice-padding pb-0">
              <div
                class="d-flex justify-content-between flex-md-row flex-column invoice-spacing mt-0"
              >
                <!-- Header: Left Content -->
                <div>
                  <b-card-text class="mb-25"> Mr. Mazzucchi </b-card-text>
                  <b-card-text class="mb-25"> Mercato X Y Z </b-card-text>
                  <b-card-text class="mb-0"> Via Strada 51, Milano </b-card-text>
                </div>

                <!-- Header: Right Content -->
                <div class="invoice-number-date mt-md-0 mt-2">
                  <div class="d-flex align-items-center justify-content-md-end mb-1">
                    <span class="title text-right"> N. Scontrino:&nbsp; </span>
                    <b-input-group
                      class="input-group-merge invoice-edit-input-group disabled"
                    >
                      <b-input-group-prepend is-text>
                        <feather-icon icon="HashIcon" />
                      </b-input-group-prepend>
                      <b-form-input
                        id="invoice-data-id"
                        v-model="receipt.number"
                        disabled
                      />
                    </b-input-group>
                  </div>
                  <div class="d-flex align-items-center justify-content-md-end mb-1">
                    <span class="title text-right"> Data:&nbsp; </span>
                    <flat-pickr
                      v-model="receipt.date"
                      class="form-control invoice-edit-input"
                      disabled
                    />
                  </div>
                </div>
              </div>
            </b-card-body>

            <!-- Spacer -->
            <hr class="invoice-spacing" />

            <!-- Items Section -->
            <b-card-body class="invoice-padding form-item-section">
              <div class="d-flex productTitles">
                <b-row class="flex-grow-1 p-0">
                  <!-- Single Item Form Headers -->
                  <b-col cols="12" lg="3"> Cassetta </b-col>
                  <b-col cols="12" lg="3"> Prodotto </b-col>
                  <b-col cols="12" lg="2"> Costo </b-col>
                  <b-col cols="12" lg="2"> Quantit (Kg.) </b-col>
                  <b-col cols="12" lg="2"> Prezzo </b-col>
                </b-row>
                <div class="form-item-action-col" />
              </div>

              <div ref="form" class="repeater-form" :style="{ height: trHeight }">
                <b-row
                  v-for="(item, index) in receipt.items"
                  :key="index"
                  ref="row"
                  class="pb-0"
                >
                  <!-- Item Form -->
                  <!-- ? This will be in loop => So consider below markup for single item -->

                  <b-col cols="12">
                    <!-- ? Flex to keep separate width for XIcon and SettingsIcon -->
                    <!-- Form Input Fields OR content inside bordered area  -->
                    <!-- ? Flex to keep separate width for XIcon and SettingsIcon -->
                    <div class="d-flex rounded">
                      <b-row class="flex-grow-1 p-0">
                        <!-- Single Item Form Headers -->
                        <b-col cols="12" lg="3">
                          <!--<label class="d-inline d-lg-none">Prodotto</label>-->

                          <v-select
                            v-model="item.item"
                            :options="availableBoxes"
                            label="code"
                            :clearable="false"
                            class="mb-2 item-selector-title"
                            placeholder="Seleziona cassetta"
                            @input="(val) => updateItem(index, val)"
                            @option:selected="processAvailableBoxes"
                          >
                            <template #option="{ code, good_id }">
                              <span>{{ code }}</span>
                              <span v-if="good_id !== null" class="text-nowrap">
                                ({{ elementAt("goods", good_id, "plural") }})</span
                              >
                            </template>
                            <span slot="no-options"
                              >Nessun altro prodotto disponibile.</span
                            >
                          </v-select>
                        </b-col>
                        <b-col cols="12" lg="3">
                          <span class="mb-1 text">
                            <span class="text-nowrap">{{
                              elementAt("goods", item.id, "plural")
                            }}</span>

                            <b-badge
                              v-if="elementAt('countries', item.origin, 'name') !== ''"
                              v-b-tooltip
                              class="infobadge infoflag"
                              :title="elementAt('countries', item.origin, 'name')"
                              variant="light-secondary"
                            >
                              <!--<feather-icon icon="GlobeIcon" />-->
                              <country-flag
                                :country="elementAt('countries', item.origin, 'code')"
                                size="small"
                                rounded
                              />
                            </b-badge>

                            <b-badge
                              v-if="
                                elementAt(
                                  'goodcategories',
                                  item.category,
                                  'description'
                                ) !== ''
                              "
                              v-b-tooltip
                              class="infobadge"
                              :title="
                                elementAt('goodcategories', item.category, 'description')
                              "
                              variant="light-secondary"
                              ><feather-icon icon="AwardIcon" />
                              <small>{{ item.category }}</small>
                            </b-badge>
                          </span>
                        </b-col>
                        <b-col cols="12" lg="2">
                          <!--<label class="d-inline d-lg-none">Costo</label>-->
                          <b-form-input
                            v-model="item.cost"
                            type="number"
                            class="mb-2"
                            @input="updateTotal"
                          />
                        </b-col>
                        <b-col cols="12" lg="2">
                          <!--<label class="d-inline d-lg-none">Qt</label>-->
                          <b-form-input
                            v-model="item.weight"
                            type="number"
                            class="mb-2"
                            :placeholder="'Max. ' + [[item.maxweight]] + ' Kg.'"
                            @input="updateTotal"
                          />
                        </b-col>
                        <b-col cols="12" lg="2">
                          <!--<label class="d-inline d-lg-none">Prezzo</label>-->
                          <span class="mb-1 text"> {{ Math.round(item.cost * item.weight  * 100) / 100}}</span>
                        </b-col>
                      </b-row>
                      <div class="d-flex flex-column justify-content-between py-50 px-25">
                        <feather-icon
                          size="32"
                          icon="XIcon"
                          class="cursor-pointer"
                          @click="removeItem(index)"
                        />
                      </div>
                    </div>
                  </b-col>
                </b-row>
              </div>
              <b-button
                v-ripple.400="'rgba(255, 255, 255, 0.15)'"
                size="sm"
                variant="primary"
                @click="addNewItem"
              >
                Aggiungi prodotto
              </b-button>
            </b-card-body>

            <!-- Invoice Description: Total -->
            <b-card-body class="invoice-padding pb-0">
              <b-row>
                <b-col
                  cols="12"
                  md="6"
                  class="mt-md-0 mt-3 d-flex align-items-center"
                  order="2"
                  order-md="1"
                />

                <!-- Col: Total -->
                <b-col
                  cols="12"
                  md="6"
                  class="mt-md-6 d-flex justify-content-end"
                  order="1"
                  order-md="2"
                >
                  <div class="invoice-total-wrapper">
                    <hr class="my-50" />
                    <div class="invoice-total-item">
                      <p class="invoice-total-title">Totale:</p>
                      <p class="invoice-total-amount"> {{ receipt.total }}</p>
                    </div>
                  </div>
                </b-col>
              </b-row>
            </b-card-body>

            <!-- Spacer -->
            <hr class="invoice-spacing" />
          </b-card>
        </b-form>
      </b-col>
    </b-row>

    <!-- Button: Send Invoice -->
    <b-button
      v-ripple.400="'rgba(255, 255, 255, 0.15)'"
      variant="primary"
      class="mb-75"
      block
      :disabled="receipt.total == 0"
      @click="issueReceipt"
    >
      Emetti scontrino
    </b-button>
  </section>
</template>

<script>
import { FormWizard, TabContent } from "vue-form-wizard";
import vSelect from "vue-select";

import { ValidationProvider, ValidationObserver } from "vee-validate";
import Cleave from "vue-cleave-component";
import Ripple from "vue-ripple-directive";

import { ref, onUnmounted } from "@vue/composition-api";
import { heightTransition } from "@core/mixins/ui/transition";

import {
  BRow,
  BCol,
  BFormGroup,
  BFormInput,
  BInputGroup,
  BInputGroupPrepend,
  BInputGroupAppend,
  BFormCheckbox,
  BForm,
  BCard,
  BCardTitle,
  BCardText,
  BCardHeader,
  BCardBody,
  BButton,
  BBadge,
  VBTooltip,
  BFormInvalidFeedback,
  BPopover,
  VBToggle,
} from "bootstrap-vue";
import { required, email } from "@validations";
import flatPickr from "vue-flatpickr-component";
import store from "@/store";
import { elementAt, boxLocationToReadable } from "../utils";

export default {
  components: {
    FormWizard,
    TabContent,
    BRow,
    BCol,
    BInputGroup,
    BFormGroup,
    BFormInput,
    BInputGroupPrepend,
    BInputGroupAppend,
    BFormCheckbox,
    BForm,
    BButton,
    BCard,
    BCardTitle,
    BCardText,
    BCardHeader,
    BCardBody,
    BBadge,
    vSelect,
    Cleave,
    BPopover,
    VBToggle,
    ValidationProvider,
    ValidationObserver,
    BFormInvalidFeedback,
    flatPickr,
  },
  directives: {
    Ripple,
    "b-tooltip": VBTooltip,
    "b-toggle": VBToggle,
  },
  mixins: [heightTransition],
  data() {
    return {
      number: {
        numeral: true,
        numeralThousandsGroupStyle: "thousand",
        autocomplete: "off",
      },
      countries: [],
      boxes: [],
      availableBoxes: [],
      goods: [],
      goodCategories: [],
      required,
      latestReceiptId: 0,
    };
  },
  watch: {
    availableBoxes(val) {
      const self = this;
      console.log("processAvailableBoxes changed:", self.availableBoxes);
    },
  },
  beforeCreate() {
    console.log(" beforeCreate");
    const self = this;

    this.$store.dispatch("boxes/get").then((result) => {
      console.log(result);
      self.boxes = result.data.data;
      self.processAvailableBoxes();
    });

    this.$store.dispatch("sales/getLatest").then((result) => {
      console.log(result);
      self.latestReceiptId = result.data.data[0].number || 0;
      self.receipt.number = self.latestReceiptId + 1;
    });
  },
  setup() {
    console.log(" setup");
    var self = this;

    const blankItem = {
      item: null,
      cost: "",
      weight: "",
      maxweight: "",
    };

    const receipt = ref({
      number: 0,
      date: new Date().toISOString().slice(0, 10),
      timestamp: new Date(),
      items: [JSON.parse(JSON.stringify(blankItem))], // ? Set single Item in form for adding data
      total: 0,
    });

    const updateItem = (index, selectedItem) => {
      console.log("item selected", selectedItem);
      receipt.value.items[index].code = selectedItem.code;
      receipt.value.items[index].cost = selectedItem.good_price;
      receipt.value.items[index].maxweight = selectedItem.good_weight;
      receipt.value.items[index].weight = "";
      receipt.value.items[index].id = selectedItem.good_id;
      receipt.value.items[index].category = selectedItem.good_category;
      receipt.value.items[index].origin = selectedItem.good_origin;
    };

    return {
      receipt,
      updateItem,
      blankItem,
    };
  },
  mounted() {
    console.log(" mounted");
    this.initTrHeight();
  },
  created() {
    console.log(" created");
    var self = this;
    window.addEventListener("resize", this.initTrHeight);

    window.$eventBus.$on("weight", (data) => {
      console.log(" received weight", data);

      var tare = 0;
      if (self.receipt.items[self.receipt.items.length - 1].item.tare) {
        tare = self.receipt.items[self.receipt.items.length - 1].item.tare;
      }
      self.receipt.items[self.receipt.items.length - 1].weight = data - tare;
      self.updateTotal();
    });

    window.$eventBus.$on("box", (data) => {
      console.log(" received box", data);
      self.receipt.items[self.receipt.items.length - 1].item = data;
      self.updateItem(self.receipt.items.length - 1, data);
      self.processAvailableBoxes();
      self.updateTotal();
    });
  },
  destroyed() {
    console.log(" destroyed");
    window.removeEventListener("resize", this.initTrHeight);
    window.$eventBus.$off("weight");
    window.$eventBus.$off("box");
  },
  methods: {
    elementAt,
    isIn(boxToSearch) {
      //console.log("2 receipt items are", this.receipt.items);

      for (var box of this.receipt.items) {
        if (box.code == boxToSearch.code) {
          return true;
        }
      }
      return false;
    },
    processAvailableBoxes() {
      var self = this;
      self.availableBoxes = [];

      //console.log("1. available boxes are", self.availableBoxes);

      for (var boxToSearch of self.boxes) {
        //console.log("a. box to search is", boxToSearch);

        if (
          "good_id" in boxToSearch &&
          boxToSearch.good_id != null &&
          boxToSearch.good_weight > 0
        ) {
          //box is not empty

          //console.log("b. box to search is not empty");

          if (!self.isIn(boxToSearch)) {
            //console.log("c. box to search is not in receipt items and can be added");

            self.availableBoxes.push(boxToSearch);
          }
        }
      }

      //console.log("------------------- Available Boxes:", self.availableBoxes);
    },
    addNewItem() {
      this.$refs.form.style.overflow = "hidden";
      this.receipt.items.push(JSON.parse(JSON.stringify(this.blankItem)));

      this.$nextTick(() => {
        this.trAddHeight(this.$refs.row[0].offsetHeight);
        setTimeout(() => {
          this.$refs.form.style.overflow = null;
        }, 350);
      });
    },
    removeItem(index) {
      this.receipt.items.splice(index, 1);
      this.trTrimHeight(this.$refs.row[0].offsetHeight);

      this.processAvailableBoxes();
    },
    updateTotal() {
      this.receipt.total = 0;

      for (var item of this.receipt.items) {
        if (item.cost && item.weight) {
          this.receipt.total += item.cost * item.weight;
        }
      }

      this.receipt.total = Math.round(this.receipt.total * 100) / 100;

      console.log(
        "update total from receipt items",
        this.receipt.items,
        this.receipt.total
      );
    },
    initTrHeight() {
      this.trSetHeight(null);
      this.$nextTick(() => {
        this.trSetHeight(this.$refs.form.scrollHeight);
      });
    },
    cleanReceipt() {
      var count = 0;
      for (var index in this.receipt.items) {
        var item = this.receipt.items[index];
        if (!item.code || !item.weight || !item.cost) {
          this.receipt.items.splice(index, 1);
          count++;
        }
      }
      if (count > 0) {
        this.trTrimHeight(this.$refs.row[0].offsetHeight);
        this.processAvailableBoxes();
      }
    },
    issueReceipt() {
      const self = this;

      console.log("items to issue receipt:", self.receipt.items);

      self.cleanReceipt();

      self.$store.dispatch("sales/add", self.receipt).then((result) => {
        console.log("- sale added", result);
        self.dataSaved();
      });
    },
    dataSaved() {
      const self = this;
      this.$swal({
        title: "Vendita completata",
        text: "Vuoi effettuare un'altra vendita?",
        icon: "success",
        showCancelButton: true,
        confirmButtonText: "Si",
        cancelButtonText: "No, torna alla home",
        customClass: {
          confirmButton: "btn btn-primary",
          cancelButton: "btn btn-outline-danger ml-1",
        },
        buttonsStyling: false,
      }).then((result) => {
        if (result.value) {
          this.$router.go(); // refresh route instead of this.$router.push({ name: "sell"}).catch(()=>{}); //catch to avoid redundant navigation
        } else {
          this.$router.push({ name: "home" });
        }
      });
    },
    handleValidation(isValid, tabIndex) {
      console.log(`Tab: ${tabIndex} valid: ${isValid}`);
    },
    handleErrorMessage(errorMsg) {
      console.log(`Error: ${errorMsg}`);
      this.errorMsg = errorMsg;
    },
  },
};
</script>

